<template>
  <div id="app">
    <v-header :seller="seller" :TIsShade="TIsShade" :FIsShade="FIsShade"></v-header>
    <div class="tab">
      <div class="tab-item">
        <router-link to="/goods" tag="div">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings" tag="div">评论</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/seller" tag="div">商家</router-link>
      </div>
    </div>
    <div class="bb" ref="bb" :style="move"></div>
    <v-shade :seller="seller" v-if="$store.state.isShade"></v-shade>
    <keep-alive include="goods">
      <router-view></router-view>
    </keep-alive>

  </div>
</template>
<script>
import { getSeller } from './api/data'
import VHeader from 'components/header/header'
import VShade from './components/shade/shade'

export default {
  data() {
    return {
      seller: {},
      trans: 0, // 设置初始偏移量
      move: {
        // 'transform': `translateX(${this.trans}%)`
      }
    }
  },
  watch: {
    $route(newPath, oldPath) {
      // 获取路由地址
      var path = newPath.path;
      var opath = oldPath.path;
      // 手动创建路由数组，获得 index 下标
      var paths = ['/goods', '/ratings', '/seller'];
      var newIndex = paths.indexOf(path);
      var oldIndex = paths.indexOf(opath);
      // 根据下标差获取点击偏移量
      this.trans = this.trans + (newIndex - oldIndex) * 100;
      // 设置绑定的 style
      this.move = {
        'transform': `translateX(${this.trans + '%'})`,
      }
    }
  },
  created() {
    var _this = this;
    getSeller(function(res) {
      _this.seller = res.data.data;
    })
    this.routerBar();
  },
  methods: {
    TIsShade() {
      console.log(1);
      this.isShade = true;
    },
    FIsShade() {
      this.isShade = false;
    },
    routerBar() {
      var paths = ['/goods', '/ratings', '/seller'];
      var path = this.$route.path;
      this.trans = paths.indexOf(path) * 100;
      this.move = {
        'transform': `translateX(${this.trans}%)`
      }
    }
  },
  components: {
    VHeader,
    VShade,
  }  
}
</script>

<style lang="scss">
@import './assets/style/global';
#app {
  .tab {
    display: flex;
    height: 80px; line-height: 80px;
    border-bottom: 1px solid rgba(7, 17, 27, .1);
    z-index: 4;
    .tab-item {
      flex: 1;
      text-align: center;
      // router-link 
      & > div {
        font-size: 28px;
        color: rgb(77, 85, 93);
        &.active {
          color: $bg;
        }
      }
    }
  }
  .active {
    // border-bottom: 3px solid red;
  }
  .bb {
    height: 1px; width: 33.3%;
    background-color: red;
    transition: all .3s ease;
  }
}
</style>
