<template>
  <div class="num-box">
    <transition>
      <img 
        class="reduce" 
        @click="reduce"
        src="../../assets/images/reduce.png" 
        v-show="isBuy"
      >
    </transition>
    <transition
      @before-enter="beforeEnter"
      @enter="enter"
      @after-enter="afterEnter">
      <div class="ball" v-show="isShow" ref="ball"></div>
    </transition>
    <span>{{ number }}</span>
    <img 
      class="add" 
      @click="add"
      src="../../assets/images/add.png" 
    >
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      number: 0,
      isBuy: false,
      isShow: false,
    };
  },
  watch: {},
  computed: {},
  created() {},
  mounted() {},
  components: {},
  methods: {
    add() {
      this.isBuy = true;
      // this.isShow = true;
      this.number++;
      this.$emit('numberAdd', this.number);
      const nums = document.getElementById('nums')
      setTimeout(()=>{
        nums.style.transform = "scale(1.5)"
        nums.style.transition = "all .3s ease"
      }, 100)
      setTimeout(()=>{
        nums.style.transform = "scale(1)"
        nums.style.transition = "all .3s ease"
      }, 300)
    },
    reduce() {
      this.number--;
      if(this.number <=0 ) {
        this.isBuy = false;
      }
      this.$emit('numberReduce', this.number);
    },
    beforeEnter(el) {
      el.style.transform = "translate(0, 0)"
      // el.style.transition = "all 1s ease"
    },
    enter(el, done) {
      // 表示动画开始之后的样式，这里可以设置小球完成动画之后的结束状态
      // el.offsetWidth
      // el.style.transform = "translate(-150px, -150px)"
      // 获取小球位置
      const ballPosition = this.$refs.ball.getBoundingClientRect();
      // 获取nums位置
      const numsPosition = document
                           .getElementById('nums')
                           .getBoundingClientRect();
      const xDist = numsPosition.left - ballPosition.left;
      const yDist = numsPosition.top - ballPosition.top;

      el.style.transform = `translate(${xDist}px, ${yDist}px)`;
                           
      el.style.transition = "all .5s ease"
      done();
    },
    afterEnter(el) {
      this.isShow = false;
      const numsPosition = document.getElementById('nums')
      setTimeout(()=>{

        numsPosition.style.transition = "all .3s ease"
        numsPosition.style.transform = "scale(1.5)";
      }, 900)
      setTimeout(()=>{
        numsPosition.style.transform = "scale(1)";
        numsPosition.style.transition = "all .3s cubic-bezier(.4, -0.3, 1, .68)"
      }, 1100)
      // numsPosition.style.transform = "scale(1)";

    }
  }
};
</script>
<style lang="scss" scoped>
.num-box {
  display: flex;
  position: relative;
  width: 100%;
  img {
    width: 35px;
    height: 35px;
    align-items: center;
    position: absolute;
    
  }
  .add {
    right: 20px;
  }
  .reduce {
    right: 100px;
  }
  span {
    position: absolute;
    right: 67px;
    bottom: -30px;
  }
  .v-enter {
    opacity: 0;
    right: 50px;
    transform: rotate(360deg);
  }
  .v-enter-active {
    transition: all .5s ease;
  }
  .ball {
    position: absolute;
    right: 8.267vw;
    width: 20px; height: 20px;
    border-radius: 50%;
    background: red;
    z-index: 999;
  }
}
</style>